.App {
  margin: 40px auto;
  width: min(85%, 750px);
}

button {
  border-radius: 4px;
  background: none;
  border: 2px solid #fff;
  color: #fff;
  cursor: pointer;
  font-size: 1em;
  font-weight: bold;
  padding: 6px 12px;
}

button:hover {
  background-color: #c23866;
  color: #fff;
}

.card-grid {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 40px;
}

@media (min-width: 800px) {
  .card-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
